import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import * as moment from 'moment';
import { HttpClient } from '@angular/common/http';
import Swal from 'sweetalert2';

@Component({
   selector: 'app-offerdetails-cmp',
   templateUrl: './offerdetails.component.html'
})
export class OfferdetailsComponent implements OnInit, OnDestroy {

   moment: any = moment;
   fullpath = null;
   user = null;
   details = null;
   sub = null;
   id = null;
   isReadMore = true;
   takeimg=3;
   showShortDesciption = true
   constructor(private router: Router,
      private routers: ActivatedRoute,
      private cdf: ChangeDetectorRef,
      private dataService: DataService,
      private http: HttpClient) {}

   ngOnInit() {
      if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
         this.user = 'exists';
      }
      else {
         this.user = null;
         localStorage.clear();
         this.router.navigate(['/host/home'])
      }
      
      const body = document.getElementsByTagName('body')[0];
      body.classList.add('lock-page');
      body.classList.add('off-canvas-sidebar');
      const card = document.getElementsByClassName('card')[0];

      this.sub = this.routers.params.subscribe(params =>
         {
           this.id = params['id'];
   
         }
      );
      
      const data = {
         id: this.id
      }
      this.dataService.post('host/get-ad-by-id', data).then((result) => {
         this.details = result.data[0];
         this.fullpath = this.details.attachments[0].path;
         let totalPrice = this.details['price']
         let price = totalPrice * (this.details['discount_of_percentage']/100)
         this.details.discountPrice = price
         this.details.totalPrice = totalPrice - price;
         if (this.details.lat != null && this.details.lat != '') {
            this.details.lat = parseFloat(this.details.lat)
        }
        if (this.details.lan != null && this.details.lan != '') {
            this.details.lan = parseFloat(this.details.lan)
        }
      })
   }
   
   ngOnDestroy() {
      const body = document.getElementsByTagName('body')[0];
      body.classList.remove('lock-page');
      body.classList.remove('off-canvas-sidebar');
   }
   
   editOffer(id){
      this.router.navigate(['hostportal/editoffer', id])
   }

   listedOffer(id){
      const data = {
         id: id,
         status: 1
      }
      this.dataService.post('host/edit-ad', data).then((result) => {
         if (result.message != 'This offer is already booked.') {
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: 'Success',
              showConfirmButton: false,
              timer: 2500
            })
            this.router.navigate(['hostportal/offerlist'])
         }
         else {
            Swal.fire({
               position: 'center',
               icon: 'error',
               title: result.message,
               showConfirmButton: false,
               timer: 2500
            })
         }
         this.cdf.detectChanges();
      },
      error => {
         Swal.fire({
            position: 'center',
            icon: 'error',
            title: 'Error',
            showConfirmButton: false,
            timer: 2500
         })
         this.cdf.detectChanges();
      }
    )

   }

   showText() {
      this.isReadMore = !this.isReadMore
   }
   

   alterDescriptionText() {
      this.showShortDesciption = !this.showShortDesciption
   }
}
