import { Routes } from '@angular/router';

import { HomeComponent } from './Home/Home.component';
import { BookinglistComponent } from './Bookinglist/Bookinglist.component';
import { BookdetailsComponent } from './Bookdetails/Bookdetails.component';
import { OfferlistComponent } from './Offerlist/Offerlist.component';
import { CreateofferComponent } from './Createoffer/Createoffer.component';
import { OfferdetailsComponent } from './offerdetails/offerdetails.component';
import { ProfileComponent } from './Profile/Profile.component';
import { InboxComponent } from './Inbox/Inbox.component';
import { EditOfferComponent } from './EditOffer/EditOffer.component';
import { DisputeDetailsComponent } from './disputeDetails/disputedetails.component';

export const HostportalRoutes: Routes = [
    {
        path: '',
        children: [ {
            path: 'bookinglist',
            component: BookinglistComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'bookdetails/:id',
            component: BookdetailsComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'disputedetails/:id',
            component: DisputeDetailsComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'offerlist',
            component: OfferlistComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'createoffer',
            component: CreateofferComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'editoffer/:id',
            component: EditOfferComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'offerdetails/:id',
            component: OfferdetailsComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'profile',
            component: ProfileComponent
               }]
    },
    {
        path: '',
        children: [ {
            path: 'inbox',
            component: InboxComponent
               }]
    }
];