import { Component, OnInit, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { AuthenticationService } from '../../_services';
import { first } from 'rxjs/operators';
import { Route, Router, ActivatedRoute, Params } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import { ChatService } from 'src/app/_chat/chat.service';
import * as moment from 'moment';
import { data } from 'jquery';
declare var $: any;

@Component({
  selector: 'app-Inbox',
  templateUrl: 'Inbox.component.html'
})

export class InboxComponent implements OnInit, AfterViewInit {
  loading = false;
  submitted = false;
  returnUrl: string;
  error = '';
  step = 1;
  bodytext = 0;
  id;
  user = null;
  allMessages = [];
  latestConversation = [];
  allmessage = [];
  moment: any = moment;
  offerMessages;
  user_id = null;
  ad_id = null;
  receiver_id = null;
  channel = null;
  publisher_id = null;
  currentchatid;
  ad;
  sender_id;
  constructor(private router: Router,
    private cdf: ChangeDetectorRef,
    private dataService: DataService,
    private chat: ChatService) {
  }

  ngOnInit() {
    // if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
    //   this.user = 'exists';
    // }
    // else {
    //   this.user = null;
    //   localStorage.clear();
    //   this.router.navigate(['/host/home'])
    // }

    console.log(localStorage.getItem('data'))
    this.user_id = JSON.parse(localStorage.getItem('data'));
    this.user_id = this.user_id.id;

    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];
    setTimeout(function () {
      // after 1000 ms we add the class animated to the login/register card
      // card.classList.remove('card-hidden');
    }, 700);

    this.dataService.get('host/get-all-messages').then((result) => {
      this.allMessages = result.data.all_messages;
      this.latestConversation = result.data.latest_conversation;
      this.getMessages(this.latestConversation[0].ad_id)
    })

    this.chat.messages.subscribe(msg => {
      // if (msg.message !== null) {
      //   $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5" style="align-self: center; width: 270px; background-color:#dcdcdc; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1">' + msg.message + '<p><span class="float-right">just now</span></p></div></div>')
      // }
      // else {
      //   $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5" style="align-self: center; width: 270px; background-color: #dcdcdc; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + msg.attachment_file + '"target="_blank">See Attachment</a><p><span class="float-right">just now</span></p></div></div>')
      // }
      if (msg.message !== null) {
        if (msg.senderid == this.sender_id) {
          $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1" style="color: white !important;">' + msg.message + '<p></p></div></div>')
        }
        else {
          $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5" style="align-self: center; width: 270px; background-color:#dcdcdc; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1">' + msg.message + '<p><span class="float-right">just now</span></p></div></div>')

        }
      }
      else {
        if (msg.senderid == this.sender_id) {
          $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + msg.attachment_file + '"target="_blank" style="color: white !important;">See Attachment</a><p></p></div></div>')
        }
        else {
          $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5" style="align-self: center; width: 270px; background-color: #dcdcdc; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + msg.attachment_file + '"target="_blank">See Attachment</a><p><span class="float-right">just now</span></p></div></div>')
        }
      }
      $('#divscroll').scrollTop(20000);
    })

    // if (localStorage.getItem('temp')) {
    //   this.getMessages(localStorage.getItem('temp'))
    // }
  }

  ngAfterViewInit() {
  }

  ngOnDestroy() {
    this.chat.leaveChannel(this.channel);

    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  changestate(val) {
    if (val == 1) {
      this.bodytext = 0;
      $('#bgchat').addClass("bgchat");
      $('#bgchat2').removeClass("bgchat");
      $('#bgchat3').removeClass("bgchat");
      $('#bgchat4').removeClass("bgchat");
    }
    else if (val == 2) {
      this.bodytext = 1;
      $('#bgchat2').addClass("bgchat");
      $('#bgchat').removeClass("bgchat");
      $('#bgchat3').removeClass("bgchat");
      $('#bgchat4').removeClass("bgchat");
    }
    else if (val == 3) {
      this.bodytext = 0;
      $('#bgchat3').addClass("bgchat");
      $('#bgchat2').removeClass("bgchat");
      $('#bgchat').removeClass("bgchat");
      $('#bgchat4').removeClass("bgchat");
    }
    else {
      this.bodytext = 1;
      $('#bgchat4').addClass("bgchat");
      $('#bgchat2').removeClass("bgchat");
      $('#bgchat3').removeClass("bgchat");
      $('#bgchat').removeClass("bgchat");
    }
  }

  showbody() {
    $('#leftmenue').addClass("d-none d-lg-block d-md-block d-xl-block");
    $('#body').removeClass("d-none d-lg-block d-md-block d-xl-block");
  }

  back() {
    $('#body').addClass("d-none d-lg-block d-md-block d-xl-block");
    $('#leftmenue').removeClass("d-none d-lg-block d-md-block d-xl-block");
  }

  getMessages(ad_id) {
    $('.emptyDiv').remove();
    const data = {
      ad_id: ad_id
    }
    this.currentchatid = ad_id;
    this.dataService.post('host/get-messages', data).then((result) => {
      let bookingDetailsLength = result.data.booking_details.length
      this.offerMessages = result.data.booking_details[bookingDetailsLength - 1];
      this.allmessage = result.data.all_message;
      this.ad = result.data.ad;
      this.receiver_id = this.offerMessages.customer_id;
      this.ad_id = this.offerMessages.ad_id;
      this.publisher_id = this.ad.user_id;
      this.sender_id = this.publisher_id;
      console.log(result)
      this.channel = this.receiver_id.toString().concat(this.publisher_id.toString(), this.ad_id.toString());
      this.chat.joinChannel(this.channel);
      this.cdf.detectChanges();
      setTimeout(function () { $('#divscroll').scrollTop(20000); }, 6000)
    })
  }

  sendMessage() {
    if ($('#sender_message').val() == null || $('#sender_message').val() == '') {
      return;
    }
    let msg = $('#sender_message').val()
    const data1 = {
      receiver_id: this.receiver_id,
      ad_id: this.ad_id,
      message: msg,
      read_status: '0',
      attachment_file: null
    }
    console.log(data1);
    this.dataService.post('host/send-a-message', data1).then((result) => {
      let userData = JSON.parse(localStorage.getItem('data'))
      let senderNameFirstChar = userData['fname'][0]
      if (result.success == true) {
        if (result.data.message != null) {
          $('#divscroll').append('<div class="col-12 emptyDiv"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1" style="color: white !important;">' + result.data.message + '<p><span class="float-right" cstyle="color: white !important>' + moment(result.data.created_at).format('ddd') + ' | ' + moment(result.data.created_at).format('MMM DD, YYYY') + ' | ' + moment(result.data.created_at).format('hh:mm a') + '</span></p></div></div>')
        }
        $('#divscroll').scrollTop(20000);
        let message = msg;
        let date = moment().format('hh:mm a')
        let data = {
          message: message,
          channel: this.channel,
          date: date,
          senderid: this.publisher_id,
          receiver_id: this.receiver_id,
          pic: '',
          attachment_file: '',
          messageId: result.data.id,
          senderNameFirstChar: senderNameFirstChar
        }
        this.chat.sendMsg("channel", data);
      }
      $('#sender_message').val(null);
      this.cdf.detectChanges();
    })
  }

  sendMessageAttachment(event: any) {
    const file: File = event.target.files[0];
    if (file) {
      const fd = new FormData();

      fd.append('receiver_id', this.receiver_id)
      fd.append('ad_id', this.ad_id)
      fd.append('read_status', '0')
      fd.append('attachment_file', file)

      this.dataService.post('host/send-a-message', fd).then((result) => {
        let userData = JSON.parse(localStorage.getItem('data'))
        let senderNameFirstChar = userData['fname'][0]
        if (result.success == true) {
          if (result.data.attachment_file != null) {
            $('#divscroll').append('<div class="col-12 emptyDiv"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + result.data.attachment_file + '"target="_blank" style="color: white !important;">See Attachment</a><p><span class="float-right" style="color: white !important;">' + moment(result.data.created_at).format('ddd') + ' | ' + moment(result.data.created_at).format('MMM DD, YYYY') + ' | ' + moment(result.data.created_at).format('hh:mm a') + '</span></p></div></div>')
            $('#divscroll').scrollTop(20000);
            let date = moment().format('hh:mm a')
            let data = {
              message: null,
              channel: this.channel,
              date: date,
              senderid: this.publisher_id,
              receiver_id: this.receiver_id,
              pic: result.data.attachment_file,
              attachment_file: '',
              messageId: result.data.id,
              senderNameFirstChar: senderNameFirstChar
            }
            this.chat.sendMsg("channel", data);
          }
        }
        $('#sender_message').val(null);
        this.cdf.detectChanges();
      })
    }
  }
}