import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';

@Component({
    selector: 'app-Offerlist-cmp',
    templateUrl: './Offerlist.component.html'
})

export class OfferlistComponent implements OnInit, OnDestroy {

    offers: any;
    user = null;
    details = null;
    listed: any = [];
    unlisted: any = [];

    constructor(private router: Router,
        private cdf: ChangeDetectorRef,
        private dataService: DataService) {
    }

    ngOnInit() {
        if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
            this.user = 'exists';
        }
        else {
            this.user = null;
            localStorage.clear();
            this.router.navigate(['/host/home'])
        }

        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.dataService.get('host/all-ads').then((result) => {
            this.offers = result.data;
            for (let i = 0; i < this.offers.length; i++) {
                let totalPrice = this.offers[i]['price']
                let price = totalPrice * (this.offers[i]['discount_of_percentage'] / 100)
                price = totalPrice - price;
                this.offers[i]['current_price'] = price;
            }
            for(let i=0; i<this.offers.length; i++){
                this.unlisted = this.offers.filter(x => x.status == 0);
                this.listed = this.offers.filter(x => x.status == 1);
            }
        })
    }

    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    getOfferDetails(id) {
        this.router.navigate(['hostportal/offerdetails', id])
    }

}
