import { HttpClient } from '@angular/common/http';
import { Component, OnInit, OnDestroy, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-Bookdetails-cmp',
    templateUrl: './Bookdetails.component.html'
})

export class BookdetailsComponent implements OnInit, OnDestroy {
    user = null;
    details = null;
    sub = null;
    id = null;
    moment: any = moment;
    showAlert = false;
    errorMessage = null;

    @ViewChild('bookingNgForm') bookingNgForm: NgForm;
    bookingForm: FormGroup;

    constructor(private router: Router,
        private routers: ActivatedRoute,
        private cdf: ChangeDetectorRef,
        private dataService: DataService,
        private _formBuilder: FormBuilder) {}

    ngOnInit() {
        if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
            this.user = 'exists';
        }
        else {
            this.user = null;
            localStorage.clear();
            this.router.navigate(['/guest/home'])
        }

        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.sub = this.routers.params.subscribe(params =>
            {
              this.id = params['id'];
            }
         );

        this.getBookedAdDetails(this.id)

        this.bookingForm = this._formBuilder.group({
            id: this.id,
            message: null
        });
    }

    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    getBookedAdDetails(id){
        const data = {
            id: id
        }
        this.dataService.post('host/get-booked-ad-by-id', data).then((result) => {
            this.details = result.data;
        })
    }

    cancelBooking(){
        this.showAlert = false;
        if (this.bookingForm.invalid) {
            Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Please Fill All Required Fields',
                showConfirmButton: false,
                timer: 2500
              })  
            this.cdf.detectChanges()
        }

        const formData = this.bookingForm.getRawValue();

        const data = {
            id: this.details.id,
            message: formData.message
        }

        this.dataService.post('guest/cancel-booked-offer', data).then((result) => {
            // console.log("cancell");
            // console.log(result);
            if (result.success == true) {
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.cdf.detectChanges();
                this.router.navigate(['hostportal/bookinglist'])
            }
        })
    }
    showinbox()
    {
        location.href='/#/hostportal/inbox'
    }
    checkIn(book_id, confirmation_number){
        const data = {
            id: book_id,
            confirmation_number: confirmation_number
        }
        this.dataService.post('host/check-in', data).then((result) => {
            if (result.success == true) {
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.getBookedAdDetails(this.id)
                this.cdf.detectChanges();
            }
        })
    }

    checkOut(book_id, confirmation_number){
        const data = {
            id: book_id,
            confirmation_number: confirmation_number
        }
        this.dataService.post('host/check-out', data).then((result) => {
            if (result.success == true) {
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.cdf.detectChanges();
                this.router.navigate(['hostportal/bookinglist'])
            }
        })
    }
}
