import { MapsAPILoader } from '@agm/core';
import { DatePipe } from '@angular/common';
import { Component, OnInit, OnDestroy, AfterViewInit, ChangeDetectorRef, ViewChild, ElementRef, NgZone } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, NgForm,Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';
declare var $: any;

@Component({
  selector: 'app-Createoffer-cmp',
  templateUrl: './Createoffer.component.html'
})

export class CreateofferComponent implements OnInit, AfterViewInit, OnDestroy {
  campaignOne: FormGroup;
  campaignTwo: FormGroup;

  check = 0;
  incval = 0;
  incval1 = 0;
  incval2 = 0;
  incval3 = 0;
  incval4 = 0;
  incval5 = 0;
  incval6 = 0;
  discountstep = 0;
  itemCounter = 0;

  items = []
  user = null;
  fileData = null;
  equipmentFeatures = null;
  languages = null;
  showAlert = false;
  errorMessage = ''
  latitude: number;
  longitude: number;
  zoom: number;
  address: string;
  private geoCoder;
  fileURLs = [];

  @ViewChild('offerNgForm') offerNgForm: NgForm;
  offerForm: FormGroup;
  @ViewChild('search')
  public searchElementRef: ElementRef;

  constructor(private router: Router,
    private cdf: ChangeDetectorRef,
    public datepipe: DatePipe,
    private _formBuilder: FormBuilder,
    private dataService: DataService,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone) {
    const today = new Date();
    const month = today.getMonth();
    const year = today.getFullYear();

    this.campaignOne = new FormGroup({
      start: new FormControl(new Date(year, month, 13)),
      end: new FormControl(new Date(year, month, 16))
    });

    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date(year, month, 15)),
      end: new FormControl(new Date(year, month, 19))
    });
  }

  ngOnInit() {
    if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
      this.user = 'exists';
    }
    else {
      this.user = null;
      localStorage.clear();
      this.router.navigate(['/host/home'])
    }

    this.dataService.get('host/get-features').then((result) => {
      this.equipmentFeatures = result.data;
    })

    this.dataService.get('host/get-all-languages').then((result) => {
      this.languages = result.data;
    })

    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    this.offerForm = this._formBuilder.group({
      title:null,
      type:null,
      price:null,
      files: null,
      guests: 0,
      rooms: 0,
      bathrooms: 0,
      single_bed: 0,
      double_bed: 0,
      large_bed: 0,
      extra_large_bed: 0,
      start_from:null,
      end_to:null,
      description:null,
      lat:null,
      lan:null,
      // address:null,
      // city:null,
      features: [],
      terms_policies:null,
      language_ids:null,
      discount_of_percentage:null,
      discount_of_booking:null,
      subsidiary:null,
      status: true
    });
   this.setformval();
  //   const $validator = $('.card-wizard form').validate({
  //     rules: {
  //       type: {
  //             required: true,
  //         },
  //         price: {
  //             required: true,
  //         },
         
  //         title:{
  //           required: true,
  //         },
  //         guests: {
  //       required: true,
  //   },
  //   rooms: {
  //       required: true,
  //   },
  //   start_from: {
  //       required: true,
  //   },
  //   description: {
  //       required: true,
  //   },
  //   features: {
  //       required: true,
  //   },
  //   terms_policies: {
  //       required: true,
  //   },
  //   discount_of_percentage: {
  //       required: true,
  //   },
  //   subsidiary: {
  //       required: true,
  //   },
  //     },

  //     highlight: function(element) {
  //       $(element).closest('.form-group').removeClass('has-success').addClass('has-danger');
  //     },
  //     success: function(element) {
  //       $(element).closest('.form-group').removeClass('has-danger').addClass('has-success');
  //     },
  //     errorPlacement : function(error, element) {
  //       $(element).append(error);
  //     }
  // });

    this.mapsAPILoader.load().then(() => {
      this.setCurrentLocation();
      this.geoCoder = new google.maps.Geocoder;

      let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement);
      autocomplete.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = autocomplete.getPlace();

          //verify result
          if (place.geometry === undefined || place.geometry === null) {
            return;
          }

          //set latitude, longitude and zoom
          this.latitude = place.geometry.location.lat();
          this.longitude = place.geometry.location.lng();
          this.zoom = 12;
        });
      });
    });
  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  ngAfterViewInit() {

  }

  nexttab() {
    this.check += 1;
    $('.nav-item > .active').parent().next('li').find('a').trigger('click');
  }
  
  slectoffer() {
    $('.card').removeClass("slectoffer");
  }

  plus(val) {
    switch (val) {
      case 0:
        this.incval += 1;
        break;
      case 1:
        this.incval1 += 1;
        break;
      case 2:
        this.incval2 += 1;
        break;
      case 3:
        this.incval3 += 1;
        break;
      case 4:
        this.incval4 += 1;
        break;
      case 5:
        this.incval5 += 1;
        break;
      case 6:
        this.incval6 += 1;
        break;
    }
  }

  minus(val) {
    switch (val) {
      case 0:
        if (val == 0) {
          if (this.incval > 0) {
            this.incval -= 1;
            break;
          }
        }
      case 1:
        if (val == 1) {
          if (this.incval1 > 0) {
            this.incval1 -= 1;
            break;
          }
        }
      case 2:
        if (val == 2) {
          if (this.incval2 > 0) {
            this.incval2 -= 1;
            break;
          }
        }
      case 3:
        if (val == 3) {
          if (this.incval3 > 0) {
            this.incval3 -= 1;
            break;
          }
        }
      case 4:
        if (val == 4) {
          if (this.incval4 > 0) {
            this.incval4 -= 1;
            break;
          }
        }
      case 5:
        if (val == 5) {
          if (this.incval5 > 0) {
            this.incval5 -= 1;
            break;
          }
        }
      case 6:
        if (val == 6) {
          if (this.incval6 > 0) {
            this.incval6 -= 1;
            break;
          }
        }
    }
  }

  publish() {
    $('#createBtn').prop('disabled', true);
    this.showAlert = false;

    let feature_ids = []
    $('.features').each(function () {
      if ($(this).is(":checked")) {
        feature_ids.push($(this).attr('name'));
      }
    })

    let language_ids = []
    $('.languages').each(function () {
      if ($(this).is(":checked")) {
        language_ids.push($(this).val());
      }
    })

    const formData = this.offerForm.getRawValue();

    let status = null;
    if (formData.status == true) {
      status = 1;
    }
    else {
      status = 0;
    }

    if (this.offerForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      $('#createBtn').prop('disabled', false);
      this.cdf.detectChanges()
      return
    }

    const data = {
      title: formData.title,
      type: formData.type,
      price: formData.price,
      files: this.fileURLs,
      guests: this.incval,
      rooms: this.incval6,
      bathrooms: this.incval1,
      single_bed: this.incval2,
      double_bed: this.incval3,
      large_bed: this.incval4,
      extra_large_bed: this.incval5,
      start_from: this.datepipe.transform(this.campaignOne.controls['start'].value, 'yyyy-MM-dd'),
      end_to: this.datepipe.transform(this.campaignOne.controls['end'].value, 'yyyy-MM-dd'),
      description: formData.description,
      lat: this.latitude.toString(),
      lan: this.longitude.toString(),
      address: $('#address').val(),
      city: $('#city').val(),
      features: feature_ids,
      terms_policies: formData.terms_policies,
      language_ids: language_ids,
      discount_of_percentage: formData.discount_of_percentage,
      discount_of_booking: formData.discount_of_booking,
      subsidiary: formData.subsidiary,
      status: status,
      currency: $('#currency').val()
    }

    this.dataService.post('host/create-ad', data).then((result) => {
      if (result.success == true) {
       
        this.clearstorage()

        if (result.message == 'Already 5 ads has been uploaded with this account') {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: result.message,
            showConfirmButton: false,
            timer: 2500
          })
          this.cdf.detectChanges();
        }
        else {
          this.showAlert = false;
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Success',
            showConfirmButton: false,
            timer: 2500
          })
          this.router.navigate(['hostportal/offerlist'])
        }
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: result.message,
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
      this.cdf.detectChanges();
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
    )
  }

  upload() {
    $('#inputhidden').click();
  }

  uploadImages() {
    this.fileData = $('#inputhidden').prop('files')
    this.itemCounter = this.fileData.length;
    let reader = new FileReader();
    for (let i = 0; i < this.fileData.length; i++) {
      let file = this.fileData[i]

      const img = new Image();
      img.src = window.URL.createObjectURL(file);

      reader.readAsDataURL(file);
      reader.onload = () => {
        setTimeout(() => {
          const width = img.naturalWidth;
          const height = img.naturalHeight;
          
          window.URL.revokeObjectURL(img.src);

          if (width <= 500 && height <= 500) {
            Swal.fire({
              position: 'center',
              icon: 'error',
              text: 'Photo should be atleast 500 x 500 size',
              showConfirmButton: false,
              timer: 2500
            })
            this.cdf.detectChanges();
            return;
          }
          const data = new FormData();
          data.append('file', this.fileData[i])
          this.dataService.post('host/upload-attachment', data).then((result) => {
            this.fileURLs.push({
              'type': 'image',
              'path': result.data,
              'caption': ''
            })
            this.cdf.detectChanges();
          },
            error => {
              Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Something went wrong. Please try again.',
                showConfirmButton: false,
                timer: 2500
              })
              this.cdf.detectChanges();
            }
          )
          this.items.push({
            'src': URL.createObjectURL(this.fileData[i]),
            'name': this.fileData[i].name,
            'id': i
          })
        }, 2000);
      };
    }
  }

  setCaption(val) {
    this.fileURLs[val]['caption'] = $('#img' + val).val();
  }

  removePicture(index) {
    $('.uploadImage' + index).remove();
    this.cdf.detectChanges()
  }

  private setCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 8;
        this.getAddress(this.latitude, this.longitude);
      });
    }
  }

  markerDragEnd($event: any) {
    this.latitude = $event.latLng.lat();
    this.longitude = $event.latLng.lng();
    this.getAddress(this.latitude, this.longitude);
    this.cdf.detectChanges()
  }

  getAddress(latitude, longitude) {
    this.geoCoder.geocode({ 'location': { lat: latitude, lng: longitude } }, (results, status) => {
      if (status === 'OK') {
        if (results[0]) {
          this.zoom = 12;
          this.address = results[0].formatted_address;
          $('#address').val(this.address)
          let add = results[1].formatted_address;
          let value = add.split(",");
          let count = value.length;
          let city = value[count - 3];
          city = city.trim()
          $('#city').val(city)
          this.cdf.detectChanges()
        } else {
          window.alert('No results found');
        }
      } else {
        window.alert('Geocoder failed due to: ' + status);
      }

    });
  }

  back() {
    this.router.navigate(['hostportal/offerlist'])
  }

  status() {
    if ($('#statusVal').is(':checked')) {
      $('#createBtn').prop('disabled', false);
    }
    else {
      $('#createBtn').prop('disabled', true);
    }
  }


///local storage set
 setformval()
 {
  this.offerForm.controls.title.setValue(localStorage.getItem("title"));
  this.offerForm.controls.type.setValue(localStorage.getItem("type"));
  this.offerForm.controls.description.setValue(localStorage.getItem("description"));
  this.offerForm.controls.price.setValue(localStorage.getItem("price"));
  $('#currency').val(localStorage.getItem("currency"));
  this.offerForm.controls.terms_policies.setValue(localStorage.getItem("terms_policies"));
  this.offerForm.controls.discount_of_percentage.setValue(localStorage.getItem("discount_of_percentage"));
  this.offerForm.controls.discount_of_booking.setValue(localStorage.getItem("discount_of_booking"));
  this.offerForm.controls.subsidiary.setValue(localStorage.getItem("subsidiary"));

  if(localStorage.getItem("start")==null)
  {
    return;
  }
  let newstartdate = new Date(localStorage.getItem("start"));
  let newenddate = new Date(localStorage.getItem("end"));
  if(newstartdate!=null && newenddate!=null)
  {
    this.campaignOne.controls['start'].setValue(newstartdate)
    this.campaignOne.controls['end'].setValue(newenddate)
  }

  // this.campaignOne.controls.start.setValue(localStorage.getItem("start"));
  // this.campaignOne.controls.end.setValue(localStorage.getItem("end"));
 }
 clearstorage()
 {
  localStorage.setItem("title",'')
  localStorage.setItem("type",'')
  localStorage.setItem("description",'')
  localStorage.setItem("price",'')
  localStorage.setItem("currency",'')
  localStorage.setItem("terms_policies",'')
  localStorage.setItem("terms_policies",'')
  localStorage.setItem("discount_of_percentage",'')
  localStorage.setItem("discount_of_booking",'')
  localStorage.setItem("subsidiary",'')
  localStorage.setItem("start",'')
  localStorage.setItem("end",'')
 }
 currency(e)
 {
  localStorage.setItem("currency",e);

 }
onkeyup($event,val)
{
  if(val==1)
  {
    localStorage.setItem("title",$event);
  }
  else if(val==2)
  {
    localStorage.setItem("type",$event);

  }
  else if(val==3)
  {
    localStorage.setItem("currency",$event);

  }
  else if(val==4)
  {
    localStorage.setItem("price",$event);

  }
  else if(val==5)
  {
    localStorage.setItem("description",$event);

  }
  else if(val==7)
  {
    localStorage.setItem("terms_policies",$event);

  }
  else if(val==8)
  {
    localStorage.setItem("discount_of_percentage",$event);

  }
  else if(val==9)
  {
    localStorage.setItem("discount_of_booking",$event);

  }
  else if(val==10)
  {
    localStorage.setItem("subsidiary",$event);

  }
  
    localStorage.setItem("start",this.campaignOne.controls['start'].value);
    localStorage.setItem("end",this.campaignOne.controls['end'].value);

}


}
