import { HttpClient } from '@angular/common/http';
import { Component, OnInit, OnDestroy, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-disputedetails-cmp',
    templateUrl: './disputedetails.component.html'
})

export class DisputeDetailsComponent implements OnInit, OnDestroy {
    user = null;
    details = null;
    sub = null;
    id = null;
    moment: any = moment;
    showAlert = false;
    errorMessage = null;
    userData = null

    constructor(private router: Router,
        private routers: ActivatedRoute,
        private cdf: ChangeDetectorRef,
        private dataService: DataService,
        private _formBuilder: FormBuilder) {}

    ngOnInit() {
        if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
            this.user = 'exists';
        }
        else {
            this.user = null;
            localStorage.clear();
            this.router.navigate(['/guest/home'])
        }

        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.sub = this.routers.params.subscribe(params =>
            {
              this.id = params['id'];
            }
         );

        const data = {
            id: this.id
        }

        this.dataService.post('host/get-dispute-by-id', data).then((result) => {
            this.details = result.data;
        })

        this.userData = JSON.parse(localStorage.getItem('data'))
    }

    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    

    withdraw(){
        const data = {
            ad_id: this.details.ad_id,
            booked_offer_id: this.details.booked_offer_id
        }
        this.dataService.post('guest/withdraw-dispute', data).then((result) => {
            if (result.success == true) {
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.router.navigate(['hostportal/bookinglist']);
            }
        })
    }

    accept(){
        const data = {
            ad_id: this.details.ad_id,
            booked_offer_id: this.details.booked_offer_id
        }
        this.dataService.post('guest/cancel-booking-cancellation', data).then((result) => {
            if (result.success == true) {
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.router.navigate(['hostportal/bookinglist']);
            }
        })
    }
}
