import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import * as moment from 'moment';
import { HttpParams } from '@angular/common/http';

@Component({
    selector: 'app-Bookinglist-cmp',
    templateUrl: './Bookinglist.component.html'
})

export class BookinglistComponent implements OnInit, OnDestroy {

    moment: any = moment;
    tempOffers = [];
    offers = [];
    user = null;
    offer0 = [];
    offer1 = [];
    offer2 = [];
    offer3 = [];
    details = [];

    constructor(private router: Router,
        private cdf: ChangeDetectorRef,
        private dataService: DataService) {
    }

    ngOnInit() {
        if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
            this.user = 'exists';
        }
        else {
            this.user = null;
            localStorage.clear();
            this.router.navigate(['/host/home'])
        }
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') { }
        else {
            localStorage.clear();
            this.router.navigate(['/host/home'])
        }

        this.dataService.get('host/booked-ads').then((result) => {
            this.tempOffers = result.data;
            for (let i = 0; i < this.tempOffers.length; i++) {
                let totalPrice = this.tempOffers[i]['price']
                let price = totalPrice * (this.tempOffers[i]['discount_of_percentage'] / 100)
                price = totalPrice - price;
                this.tempOffers[i]['current_price'] = price;
            }

            for(let j=0; j<this.tempOffers.length; j++){
                for(let k=0; k<this.tempOffers[j].booking_details.length; k++){
                    this.offers.push({
                        id: this.tempOffers[j].booking_details[k].id,
                        attachment: this.tempOffers[j].attachments[0].path,
                        title: this.tempOffers[j].title,
                        start_from: this.tempOffers[j].start_from,
                        end_to: this.tempOffers[j].end_to,
                        current_price: this.tempOffers[j].current_price,
                        guests: this.tempOffers[j].guests,
                        confirmation_number: this.tempOffers[j].booking_details[k].confirmation_number,
                        status: this.tempOffers[j].booking_details[k].status,
                        currency: this.tempOffers[j].currency
                    })
                }
            }

            for(let i=0; i<this.offers.length; i++){
                this.offer0 = this.offers.filter(x => x.status == 0);
                this.offer1 = this.offers.filter(x => x.status == 1);
                this.offer2 = this.offers.filter(x => x.status == 2);
            }
        })

        this.dataService.get('host/get-all-disputes').then((result) => {
            this.offer3 = result.data;
            for (let i = 0; i < this.offer3.length; i++) {
                let totalPrice = this.offer3[i]['offer']['price']
                let price = totalPrice * (this.offer3[i]['offer']['discount_of_percentage'] / 100)
                price = totalPrice - price;
                this.offer3[i]['offer']['current_price'] = price;
            }
        })

    }

    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    getBookingDetails(id) {
        this.router.navigate(['hostportal/bookdetails', id])
    }

    disputeDetails(id) {
        this.router.navigate(['hostportal/disputedetails', id])
    }
}
