import { DatePipe } from '@angular/common';
import { MapsAPILoader } from '@agm/core';
import { Component, OnInit, OnDestroy, AfterViewInit, ChangeDetectorRef, ViewChild, NgZone, ElementRef } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';
declare var $: any;

@Component({
  selector: 'app-EditOffer-cmp',
  templateUrl: './EditOffer.component.html'
})

export class EditOfferComponent implements OnInit, AfterViewInit, OnDestroy {
  campaignOne: FormGroup;
  campaignTwo: FormGroup;

  check = 0;
  incval = 0;
  incval1 = 0;
  incval2 = 0;
  incval3 = 0;
  incval4 = 0;
  incval5 = 0;
  incval6 = 0;
  discountstep = 0;
  itemCounter = 0;

  items = []
  user = null;
  fileData = null;
  equipmentFeatures = null;
  languages = null;
  showAlert = false;
  errorMessage = ''
  latitude: number;
  longitude: number;
  zoom: number;
  address: string;
  private geoCoder;
  fileURLs = [];
  sub = null;
  id = null;
  details = []
  imageCheck = 0;
  ad_id = null;
  map = 0;
  currency1;
  @ViewChild('editNgForm') editNgForm: NgForm;
  editForm: FormGroup;
  @ViewChild('search')
  public searchElementRef: ElementRef;

  constructor(private router: Router,
    private cdf: ChangeDetectorRef,
    public datepipe: DatePipe,
    private routers: ActivatedRoute,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    private _formBuilder: FormBuilder,
    private dataService: DataService) {
    const today = new Date();
    const month = today.getMonth();
    const year = today.getFullYear();
    this.campaignOne = new FormGroup({
      start: new FormControl(new Date(year, month, 13)),
      end: new FormControl(new Date(year, month, 16))
    });
  }

  ngOnInit() {
    if (localStorage.getItem("token") != null && localStorage.getItem('role') == 'host') {
      this.user = 'exists';
    }
    else {
      this.user = null;
      localStorage.clear();
      this.router.navigate(['/host/home'])
    }

    this.dataService.get('host/get-features').then((result) => {
      this.equipmentFeatures = result.data;
    })

    this.dataService.get('host/get-all-languages').then((result) => {
      this.languages = result.data;
    })

    this.editForm = this._formBuilder.group({
      title: null,
      type: null,
      price: null,
      files: null,
      guests: 0,
      rooms: 0,
      bathrooms: 0,
      single_bed: 0,
      double_bed: 0,
      large_bed: 0,
      extra_large_bed: 0,
      start_from: null,
      end_to: null,
      description: null,
      lat: null,
      lan: null,
      address: null,
      city: null,
      features: [],
      terms_policies: null,
      language_ids: null,
      discount_of_percentage: null,
      discount_of_booking: null,
      subsidiary: null,
      status: null
    });

    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    this.sub = this.routers.params.subscribe(params => {
      this.id = params['id'];
    });

    const data = {
      id: this.id
    }

    this.dataService.post('host/get-ad-by-id', data).then((result) => {
      this.details = result.data[0];
      this.ad_id = this.details['id']
      this.editForm.controls['title'].setValue(this.details['title'])
      this.editForm.controls['type'].setValue(this.details['type'])
      this.editForm.controls['price'].setValue(this.details['price'])
      this.editForm.controls['description'].setValue(this.details['description'])
      this.editForm.controls['terms_policies'].setValue(this.details['terms_policies'])
      this.editForm.controls['status'].setValue(this.details['status'])

      this.editForm.controls['discount_of_percentage'].setValue(this.details['discount_of_percentage'])
      this.editForm.controls['discount_of_booking'].setValue(this.details['discount_of_booking'])
      this.editForm.controls['subsidiary'].setValue(this.details['subsidiary'])

      this.editForm.controls['address'].setValue(this.details['address'])
      $('#address').val(this.details['address'])
      this.editForm.controls['city'].setValue(this.details['city'])
      $('#city').val(this.details['city'])
      this.editForm.controls['lat'].setValue(this.details['lat'])
      this.latitude = parseFloat(this.details['lat'])
      this.editForm.controls['lan'].setValue(this.details['lan'])
      this.longitude = parseFloat(this.details['lan'])

      $('#currency').val(this.details['currency'])
      this.currency1=this.details['currency'];
      this.editForm.controls['start_from'].setValue(this.details['start_from'])
      this.editForm.controls['end_to'].setValue(this.details['end_to'])
      this.campaignOne.controls['start'].setValue(this.details['start_from'])
      this.campaignOne.controls['end'].setValue(this.details['end_to'])

      this.editForm.controls['guests'].setValue(this.details['guests'])
      this.incval = this.details['guests']
      this.editForm.controls['rooms'].setValue(this.details['rooms'])
      this.incval6 = this.details['rooms']
      this.editForm.controls['bathrooms'].setValue(this.details['bathrooms'])
      this.incval1 = this.details['bathrooms']
      this.editForm.controls['single_bed'].setValue(this.details['single_bed'])
      this.incval2 = this.details['single_bed']
      this.editForm.controls['double_bed'].setValue(this.details['double_bed'])
      this.incval3 = this.details['double_bed']
      this.editForm.controls['large_bed'].setValue(this.details['large_bed'])
      this.incval4 = this.details['large_bed']
      this.editForm.controls['extra_large_bed'].setValue(this.details['extra_large_bed'])
      this.incval5 = this.details['extra_large_bed']

      //features formcontrol will be managed directly when updating
      this.editForm.controls['language_ids'].setValue(this.details['languages'])
      let j = 1
      do {
        this.dataService.get('host/get-all-languages').then((result) => {
          let langList = result.data;
          if (this.details['languages'] != null) {
            for (let i = 0; i < langList.length; i++) {
              if (this.details['languages'][i]['lang_id'] == langList[i]['id']) {
                $('#lang' + (i + 1)).prop('checked', true)
              }
            }
          }
        })
        this.dataService.get('host/get-features').then((result) => {
          let eFeatures = result.data;
          for (let i = 0; i < eFeatures.length; i++) {
            if (this.details['features'][i]['feature_id'] == eFeatures[i]['id']) {
              $('input[name="feature' + (i + 1) + '"]').prop('checked', true)
            }
          }
        })
        for (let k = 0; k < this.details['attachments'].length; k++) {
          this.fileURLs.push({
            'type': 'image',
            'path': this.details['attachments'][k]['path'],
            'caption': this.details['attachments'][k]['caption']
          })
        }
      } while (j < 1)
    })

    this.mapsAPILoader.load().then(() => {
      this.geoCoder = new google.maps.Geocoder();
      if (this.map == 0) {
        this.longitude = parseFloat(this.details['lan'])
        this.latitude = parseFloat(this.details['lat'])
        this.zoom = 8;
        this.map = 1
      }
      else {
        this.setCurrentLocation();
        this.geoCoder = new google.maps.Geocoder;

        let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement);
        autocomplete.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = autocomplete.getPlace();

            //verify result
            if (place.geometry === undefined || place.geometry === null) {
              return;
            }

            //set latitude, longitude and zoom
            this.latitude = place.geometry.location.lat();
            this.longitude = place.geometry.location.lng();
            this.zoom = 12;
          });
        });
      }
    });

  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  ngAfterViewInit() {

  }

  nexttab() {
    this.check += 1;
    $('.nav-item > .active').parent().next('li').find('a').trigger('click');
  }

  slectoffer() {
    $('.card').removeClass("slectoffer");
  }

  plus(val) {
    switch (val) {
      case 0:
        this.incval += 1;
        break;
      case 1:
        this.incval1 += 1;
        break;
      case 2:
        this.incval2 += 1;
        break;
      case 3:
        this.incval3 += 1;
        break;
      case 4:
        this.incval4 += 1;
        break;
      case 5:
        this.incval5 += 1;
        break;
      case 6:
        this.incval6 += 1;
        break;
    }
  }

  minus(val) {
    switch (val) {
      case 0:
        if (val == 0) {
          if (this.incval > 0) {
            this.incval -= 1;
            break;
          }
        }
      case 1:
        if (val == 1) {
          if (this.incval1 > 0) {
            this.incval1 -= 1;
            break;
          }
        }
      case 2:
        if (val == 2) {
          if (this.incval2 > 0) {
            this.incval2 -= 1;
            break;
          }
        }
      case 3:
        if (val == 3) {
          if (this.incval3 > 0) {
            this.incval3 -= 1;
            break;
          }
        }
      case 4:
        if (val == 4) {
          if (this.incval4 > 0) {
            this.incval4 -= 1;
            break;
          }
        }
      case 5:
        if (val == 5) {
          if (this.incval5 > 0) {
            this.incval5 -= 1;
            break;
          }
        }
      case 6:
        if (val == 6) {
          if (this.incval6 > 0) {
            this.incval6 -= 1;
            break;
          }
        }
    }
  }

  update() {
    $('#updateBtn').prop('disabled', true);
    this.showAlert = false;

    let feature_ids = []
    $('.features').each(function () {
      let temp = $(this).attr('name')
      temp = temp.replace('feature', '')
      if ($(this).is(":checked")) {
        feature_ids.push(temp);
      }
    })

    let language_ids = []
    $('.languages').each(function () {
      if ($(this).is(":checked")) {
        language_ids.push($(this).val());
      }
    })

    if (this.editForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      $('#updateBtn').prop('disabled', false);
      this.cdf.detectChanges()
    }

    const formData = this.editForm.getRawValue();

    let status = null;
    if (formData.status == true) {
      status = 1;
    }
    else {
      status = 0;
    }

    const data = {
      id: this.ad_id,
      title: formData.title,
      type: formData.type,
      price: formData.price,
      files: this.fileURLs,
      guests: this.incval,
      rooms: this.incval6,
      bathrooms: this.incval1,
      single_bed: this.incval2,
      double_bed: this.incval3,
      large_bed: this.incval4,
      extra_large_bed: this.incval5,
      start_from: this.datepipe.transform(this.campaignOne.controls['start'].value, 'yyyy-MM-dd'),
      end_to: this.datepipe.transform(this.campaignOne.controls['end'].value, 'yyyy-MM-dd'),
      description: formData.description,
      lat: this.latitude.toString(),
      lan: this.longitude.toString(),
      address: $('#address').val(),
      city: $('#city').val(),
      features: feature_ids,
      terms_policies: formData.terms_policies,
      language_ids: language_ids,
      discount_of_percentage: formData.discount_of_percentage,
      discount_of_booking: formData.discount_of_booking,
      subsidiary: formData.subsidiary,
      status: status,
      currency: $('#currency').val()
    }

    this.dataService.post('host/edit-ad', data).then((result) => {
      if (result.success == true) {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Success',
          showConfirmButton: false,
          timer: 2500
        })
        this.router.navigate(['hostportal/offerlist'])
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: result.message,
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
      this.cdf.detectChanges();
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Pelase try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
    )
  }

  upload() {
    $('#inputhidden').click();
  }

  uploadImages() {
    this.fileData = $('#inputhidden').prop('files')
    this.itemCounter = this.fileData.length
    let reader = new FileReader();
    for (let i = 0; i < this.fileData.length; i++) {
      let file = this.fileData[i]

      const img = new Image();
      img.src = window.URL.createObjectURL(file);

      reader.readAsDataURL(file);
      reader.onload = () => {
        setTimeout(() => {
          const width = img.naturalWidth;
          const height = img.naturalHeight;

          window.URL.revokeObjectURL(img.src);

          if (width <= 500 && height <= 500) {
            Swal.fire({
              position: 'center',
              icon: 'error',
              text: 'Photo should be atleast 500 x 500 size',
              showConfirmButton: false,
              timer: 2500
            })
            this.cdf.detectChanges();
            return;
          }
          const data = new FormData();
          data.append('file', this.fileData[i])
          this.dataService.post('host/upload-attachment', data).then((result) => {
            this.fileURLs.push({
              'type': 'image',
              'path': result.data,
              'caption': ''
            })
            this.cdf.detectChanges();
          },
            error => {
              Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Something went wrong. Please try again.',
                showConfirmButton: false,
                timer: 2500
              })
              this.cdf.detectChanges();
            }
          )
          this.items.push({
            'src': URL.createObjectURL(this.fileData[i]),
            'name': this.fileData[i].name,
            'id': i
          })
        }, 2000);
      };
    }
  }

  setCaption(val) {
    this.fileURLs[val]['caption'] = $('#img' + val).val();
  }

  removePicture(index) {
    this.fileURLs.splice(index);
    $('.uploadImage' + index).remove();
    this.cdf.detectChanges()
  }

  private setCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 8;
        this.getAddress(this.latitude, this.longitude);
      });
    }
  }

  markerDragEnd($event: any) {
    this.latitude = $event.latLng.lat();
    this.longitude = $event.latLng.lng();
    this.getAddress(this.latitude, this.longitude);
    this.cdf.detectChanges()
  }

  getAddress(latitude, longitude) {
    this.geoCoder.geocode({ 'location': { lat: latitude, lng: longitude } }, (results, status) => {
      if (status === 'OK') {
        if (results[0]) {
          this.zoom = 12;
          this.address = results[0].formatted_address;
          $('#address').val(this.address)
          let add = results[1].formatted_address;
          let value = add.split(",");
          let count = value.length;
          let city = value[count - 3];
          city = city.trim()
          $('#city').val(city)
          this.cdf.detectChanges()
        } else {
          window.alert('No results found');
        }
      } else {
        window.alert('Geocoder failed due to: ' + status);
      }

    });
  }

  back() {
    this.router.navigate(['hostportal/offerlist'])
  }

  status() {
    if ($('#statusVal').is(':checked')) {
      $('#updateBtn').prop('disabled', false);
    }
    else {
      $('#updateBtn').prop('disabled', true);
    }
  }
}
